
<template>
  <div :class="`${prefixCls}-checkbox-group`">
    <Checkbox v-for="(item,index) in data"
              :key="index"
              :value="item.value"
              :label="item.label||item.value"
              :disabled="disabled||item.disabled"
              :class="item.class"
              v-model="groupValue"
              @change="_change">
    </Checkbox>
  </div>
</template>
<script>
import Checkbox from './Checkbox'
// import {prefixCls} from '../prefix'

export default {
  name: 'vi-checkbox-group',
  data () {
    return {
      prefixCls: 'vi',
      groupValue: this.value // 
    }
  },
  watch: {
    value (v) {
      this.groupValue = v
    }
  },
  updated () {
  },
  props: {
    data: Array,
    value: Array,
    name: String,
    max: Number,
    min: Number,
    change: Function,
    disabled: {
      type: Boolean,
      default: false
    }
  },
  components: { Checkbox },
  methods: {
    _change (v) {
      const newLen = this.groupValue.length
      const len = this.value.length
      let isAdd = false
      if (newLen > len) {
        isAdd = true // 
      }
      // 
      if ((isAdd && len >= this.max) || (!isAdd && len <= this.min)) {
        this.$emit('input', [...this.value])
      } else {
        this.$emit('input', [...this.groupValue])
      }
      this.change && this.change(v)
    },
    select () {
      // 
      // value
      let value = []
      this.data && this.data.forEach(item => {
        if (item.disabled && this.value.indexOf(item.value) === -1) {
          // 
        } else {
          value.push(item.value)
        }
      })
      this.$emit('input', value)
    },
    clear () {
      // 
      // console.log('clear')
      // 
      let value = []
      this.data && this.data.forEach(item => {
        if (item.disabled && this.value.indexOf(item.value) !== -1) {
          // 
          value.push(item.value)
        }
      })
      this.$emit('input', value)
    }
  },
  computed: {},
  mounted () {
  },
  filters: {}
}
</script>
