<template>
  <!--  -->
  <transition name="fade-in">
    <div class="vi-back-top"
         v-if="visible"
         @click="handleClick">
      <slot>
        <div class="vi-back-top-target"></div>
      </slot>
    </div>
  </transition>

</template>
<script>
export default {
  name: `vi-backtop`,
  props: {
    // $el
    target: {
      validator (node) {
        return !node || node.addEventListener
      }
    },
    // !
    offset: {
      type: Number,
      default: 60
    }
  },
  data () {
    return {
      visible: false, // 
      container: null // 
    }
  },
  watch: {
    // target
    target () {
      this.bindContainer()
    }
  },
  mounted () {
    this.bindContainer()
  },
  beforeDestroy () {
    this.unbindListener()
  },
  methods: {
    // 
    bindContainer () {
      if (this.container) {
        this.unbindListener()
      }
      this.container = this.target || this.$parent.$el
      this.bindListener()
    },
    // 
    bindListener () {
      this.container.addEventListener('scroll', this.handleScroll)
    },
    // 
    unbindListener () {
      if (this.container) {
        this.container.removeEventListener('scroll', this.handleScroll)
        this.container = null
      }
    },
    // 
    handleClick () {
      // 
      this.container.scrollTo({ top: 0, behavior: 'smooth' })
      this.$emit('click')
    },
    // 
    handleScroll ({ target }) {
      this.visible = target.scrollTop > this.offset
    }
  }
}
</script>
