export function getFileBase64 (file) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader()
    reader.readAsDataURL(file)
    reader.onload = function () {
      // file.src = this.result;  //base64
      resolve(this.result)
    }
  })
}
