<template>
  <ul :class="classes"><slot></slot></ul>
</template>
<script>
import { oneOf } from '../../utils/assist'
import emitter from '../../mixins/emitter'
export default {
  name: 'vi-menu',
  mixins: [emitter],
  props: {
    mode: {
      validator (value) {
        return oneOf(value, ['horizontal', 'vertical'])
      },
      default: 'vertical'
    },
    theme: {
      validator (value) {
        return oneOf(value, ['light', 'dark'])
      },
      default: 'light'
    },
    value: {
      type: [String, Number],
      default: ''
    },
    openNames: {
      type: Array,
      default () {
        return []
      }
    },
    accordion: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      activeName: this.value
    }
  },
  computed: {
    classes () {
      return [
        'vi-menu',
        `vi-menu-${this.mode}`,
        `vi-menu-${this.theme}`
      ]
    }
  },
  watch: {
    /**  **/
    value (value) {
      if (value !== this.activeName) {
        this.activeName = value
      }
    },
    /**  **/
    activeName () {
      this.broadcastActive()
    }
  },
  mounted () {
    this.$on('menu-item-active', this.handleUpdateActive)
    this.$on('sub-menu-active', this.handleUpdateOpened)
    this.broadcastActive()
    // 
    this.broadcast('vi-sub-menu', true, 'update-active', this.openNames)
  },
  methods: {
    // 
    handleUpdateActive (name) {
      this.activeName = name
      this.$emit('input', name)
      this.$emit('change', name)
    },
    // 
    handleUpdateOpened (name) {
      // 
      if (this.accordion) {
        this.broadcast('vi-sub-menu', false, 'update-active', [name])
      }
    },
    broadcastActive () {
      this.broadcast('vi-menu-item', true, 'update-active', this.activeName)
    }
  }
}
</script>
