<template>
  <div class="vi-upload-drop">
    <div class="vi-upload-content">
      <div class="vi-drag-area"
           @dragover.prevent="fileDragover"
           @drop.prevent="fileDrop">
        <div class="vi-upload-tips">
          <span></span>
          <label for="fileInput"></label>
        </div>
      </div>
    </div>
    <div class="vi-upload-footer">
      <input type="file"
             id="fileInput"
             @change="chooseUploadFile"
             style="display: none;">
      <!-- <label for="fileInput" v-if="fileName" style="color: #11A8FF; cursor: pointer"></label> -->
      <div :class="['vi-file-name',activeNum==index ?'active' : '']"
           v-for="(item,index) in fileName"
           :key="index"
           @mouseover="mouseOver(index)"
           @mouseout="mouseOut">
        <p class="vi-file-txt">{{item.file.name}}</p>
        <i class='vi-file-icon'
           @click="deleteChange(index)">{{activeNum==index ? 'X' : ''}}</i>
      </div>
      <!-- <button @click="uploadOk"></button> -->
    </div>
  </div>
</template>
<script>
export default {
  name: 'vi-dropload',
  props: {
    maxfileSize: {
      type: Number,
      default: 2 * 1024 * 1024
    }
  },
  data () {
    return {
      fileName: [],
      activeNum: null
    }
  },
  methods: {
    mouseOver (index) {
      this.activeNum = index
    },
    mouseOut () {
      this.activeNum = null
    },
    // 
    chooseUploadFile (e) {
      const file = e.target.files.item(0)
      if (!file) return
      if (file.size > this.maxfileSize) {
        console.log('2M!')
        return
      }
      this.fileName.push({ file })
      this.uploadOk()
      // 
      e.target.value = ''
    },
    // 
    fileDragover (e) {
      // console.log("")
    },
    fileDrop (e) {
      const file = e.dataTransfer.files[0] // 
      if (!file) return
      if (file.size > this.maxfileSize) {
        console.log('2M')
        return
      }
      this.fileName.push({ file })
      this.uploadOk()
      // console.log(this.fileName)
    },
    //         fileDrop chooseUploadFile
    uploadOk () {
      this.$emit('fileImg', this.fileName)
      // if (this.fileName &&this.fileName.length >0){
      //   return alert('')
      // }
      // let data = new FormData()
      // data.append('upfile', this.batchFile)
      // ajax
    },
    deleteChange (num) {
      if (this.activeNum === num) {
        this.fileName.splice(num, 1)
      }
    }
  }
}
</script>
